.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  padding: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.api-results-box {
  border-width: 1px;
  border-style: solid;
  padding: 20px;
  margin-right: 20px;
  width: 300px;
}

.dropdown {
  padding: 20px;
  margin-left: 20px;
  width: 300px;
  font-size: 20px;
}

.dropdown:hover {
  cursor: pointer;
}

a {
  color: black;
}

a:hover {
  color: blue;
  cursor: pointer;
}

.dropdown-menu {
  border-width: 1px;
  border-style: solid;
  background-color: white;
  padding: 10px;
  z-index: 99;
}
/* style={{borderWidth: 1, borderStyle: 'solid', zIndex: 99}}  */

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
